.Product{
    display: flex;
    flex-direction:column;
    align-items: center;
    width: 100%;
    max-height: 500px;
    min-width:400px;
    background-color:white;
    z-index:1;
    margin-bottom: 20px;

}
.product_rating{
    display:flex;
    
}

.Product > img {
    max-height:200px;
    width:100%;
    object-fit: contain;
    margin-bottom:20px;
    margin-top:20px;
    background-color:whitesmoke;
}
.Product > button {
    background-color:goldenrod;
    margin-top: 10px;
    margin-bottom:30px;
    color:white;
    border: none;
    border-radius: 5px;
    cursor: pointer;
    width:120px;

}
.Product > button:hover {
    background-color:darkgoldenrod;


}
.product_price{
    margin-top:10px;
}
.product_info{
    height:100px;
    margin-bottom: 1px;

}
.product_info > p{
    font-size:25px;
    
}