.address{
    position:fixed;
    top:0; left:0; right:0; bottom:0;
    background: rgba(0,0,0,0.6);    
   
}
.address_modal{

    position:absolute;
    top:50%; left: 50%;
    transform: translate(-50%,-50%);
    width:480px;
    height:700px;
    background-color:white;
    align-items: center;
    display: flex;
    flex-direction: column;
    justify-content: center;
    border-radius:5px;
}
.address_title{
    
    display: flex;
    background-color:gainsboro;
    width:100%;
    height:80px;
    align-items: center;
    justify-content: center;

}
.address_citycode{
    display: flex;
}
.address_citytitle{
    display: flex;
    gap:150px;
}