.payment{
    width:100%;
    background-color:white;
    height:100%;
}


.payment_background{
    background-color: white;
    display:flex;
    max-width: 1200px;
    margin:auto;
    height:100vh;
    
}


.payment_section{

    display: flex;
    padding: 10px;
    margin: 0 10px;

}



.payment_items, .payment_detail{

    flex:0.8;
}

.payment_detail > form > button {
    background:darkgoldenrod;
    border:none;
    width:250px;
    height:30px;
    margin-top:10px;
    margin-bottom: 10px;
    border-radius:5px;
    cursor:pointer;
}
.payment_detail > form > button:disabled{
    background:burlywood;
    cursor:not-allowed;
}
.payment_container{
    overflow: hidden;
    width:850px;
}
.payment_left{
    flex:0.8;
   
  
    
   
    
}
.payment_right{
    display: flex;
    flex-direction: column;
    flex:0.2;
    border: 1px solid lightgray;
    margin:50px 10px 20px 0px;
    height:300px;
   
}
.payment_address{
    
    margin:10px;
    border: 1px solid silver;
    display: flex;
    flex-direction: column;
    border-radius: 5px;
    align-items: center;
    

}
.payment_address_name{
    color:goldenrod;
}


.payment_address_one {
    display: flex;
    border-bottom: 1px solid silver;
    width:95%;
    padding: 5px 5px;
}

.iconlabel{
    
    margin-top: 10px;
}
.payment_address > label {
    margin-left: 45px;
    margin-bottom: 10px;
    font-size:14px;
    display: flex;
    
    
}
.payment_address > label > input {
    margin-right:10px;
    margin-top:2px;
}
.payment_address_two{
    display: flex;
    width:95%;
    margin-left:7px;
    margin-top: 10px;
    
}
.payment_address_two >a{
    color:cadetblue;
    cursor:pointer;

}
.payment_address_two > a:hover{
    color:darkgoldenrod;
    text-decoration-line: underline;
}

.addicon{
    color:gainsboro;
    
}
.payment_address_section{
    display: flex;
    height: 40px;
    background-color:rgba(255, 228, 196, 0.397);
    width:95%;
    border-radius: 5px;
    border:1px solid burlywood;
    margin-top:10px;
    align-items: center;

}
.payment_address_box >input[type="checkbox"]{
    border-radius: 100%;
    appearance: none;
    width:1rem;
    height: 1rem;
    border:1px solid steelblue;
    transition:background 0.2s;
    margin-top:3px;

}
.payment_address_box >input[type="checkbox"]:checked{
    background: steelblue;
    box-shadow: inset 0px 0px 1.5px 1.5px rgb(255, 255, 255)
    
}
.payment_address_box{
    margin-left: 10px;
    display: flex;
}
.payment_address_box >input[type="checkbox"]:hover{
    background: rgb(13, 100, 172);
    box-shadow: inset 0px 0px 1.5px 1.5px rgb(255, 255, 255);
    border:1px solid rgb(13, 100, 172);

    
}
.payment_address_box > span{
    margin-left:10px;
    
}
.payment_address_footer{
    margin-top: 50px;
    height: 60px;
    background-color:rgba(220, 220, 220, 0.473);
    width: 100%;
    display: flex;
    align-items:center;
    border-top: 1px solid silver;
    border-radius: 5px;
}
.payment_address_footer > button {
    margin-left: 20px;
    height: 35px;
    border-radius: 8px;
    cursor: pointer;
    background-color:goldenrod;
    border: none;
    width:120px;
    font-weight: bold;
}
.payment_address_footer > button:hover{
    background-color:darkgoldenrod;

}
.payment_address_close{
    width:95%;
    position:relative;
    display:flex;
    flex-direction: row;
    gap:50px;
    height: 100%;
    float:left;
    top:-250px;
    border-bottom: 1px solid silver;
    
}
.payment_address_list > ul {
    list-style-type:none;
    margin-bottom: 10px;

}
.payment_address_change > span {
    color:cadetblue;
    cursor: pointer;
    margin-left:390px;
    
}
.payment_address_change > span:hover{
    color:darkgoldenrod;
    text-decoration-line: underline;
    
}
.payment_items_close {
    
    height: 100px;
    width:95%;
    border-bottom: 1px solid silver;

}
.payment_items_closename >h3{
    margin-top:5px;
    position:absolute;
}
.payment_items{
    position:relative;
    top:-230px;
    width:95%;
    

}
.payment_items_name{
    color:goldenrod;
    margin-bottom: 10px;

}
.payment_items_wrapper{
    border:1px solid silver;
    border-radius: 5px;
    min-height: 300px;
    
    
}


.payment_items_section{
    display: flex;
    
    
}
.payment_items_left{
    flex:0.5;
    

}
.payment_items_right{
    
    flex:0.5;
}
.payment_items_date{
    margin-left:10px;
    margin-top:20px;
    color:darkcyan;
    font-weight:bold;
}

