.homeslider{
    max-width:1505px;
    margin-bottom: -180px;
    mask-image:linear-gradient(to bottom,rgba(0,0,0,1)50%,rgba(0, 0, 0, 0)); 
  }  

.home_image{
    width: 100%;
    z-index: -1;
    margin-bottom: -150px;
    

}


.Rarrowicon{
    position: absolute;
    text-align: right;
    right: 20px;
    color: white;
    top:250px;
    cursor: pointer;
    
}
.Larrowicon{
    position: absolute;
    text-align: left;
    left: 20px;
    color:white;
    top:250px;
    cursor: pointer;
    
    
}
.Larrowicon:hover{
    filter: drop-shadow(3px 5px 2px rgba(0,0,0));
    color:rgb(133, 217, 245);
    transition:0.2s ;

    
}
.Rarrowicon:hover{
    filter: drop-shadow(3px 5px 2px rgba(0,0,0));
    color:rgb(133, 217, 245);
    transition:0.2s ;

    
}