.checkoutProduct{
    display:flex;
    margin-bottom: 20px;
    border-bottom: 1px solid silver;
    padding: 10px
    
}


.checkoutproduct_info{

    padding-left: 20px;
    


}
.checkoutproduct_info> span:hover{
    text-decoration: underline;
    cursor: pointer;

}
.checkoutproduct_info> span{
    margin:10px;
    
    color:cadetblue;

}
.checkoutproduct_image{
    object-fit: contain;
    width:140px;
    height:140px;
}
.checkoutproduct_info > p {
    padding-top:5px;

}
.checkoutproduct_rating{
    display:flex;
    
}
.checkoutproduct_checkbox{
    display:flex;
    align-items:center;
    margin-right:15px;

    



}
.checkoutproduct_checkbox > input[type="checkbox"]{
    appearance:none;
    cursor:pointer;
    width:1rem;
    height:1rem;
    background:white;
    border:1px solid rgba(0, 139, 139, 0.63);
    text-align:center;

}

.checkoutproduct_checkbox > input[type="checkbox"]:checked{
   background:cadetblue;
   


}
.checkoutproduct_checkbox > input[type="checkbox"]:before{
    content:"\2714";
    overflow:hidden;
    color:white;
    
    transform: scale(0) translate(-50%,-50%);
    line-height: 1;


}