.nav{
    margin:0;
    display:flex;
    background-color:rgb(228, 227, 224);
    height:36px;
    align-items:center;
}
.nav_service{
    display:flex;
    font-weight: 600;
    font-size: 14px;
    gap:15px;
    margin-bottom:2px;
    
}
.nav_service > a{
    text-decoration:none;
    color:black;
}
.nav_service > a:hover{
    
    border:1px solid black;
}
.nav_index{
    margin-left: 17px;
    margin-right: 20px;
    display:flex;
    align-items:center;

}
.nav_index >p{
    margin-left:3px;
    font-size: 15px;
    font-weight: 600;
    
}
