.Checkout{
    display: flex;
    padding: 20px;
    height: max-content;
}
.checkout_title{
    margin-right: 20px;
    margin-left:20px;
    
    
}

.checkout_left{
    margin-top: 20px;
    background-color:white;
    flex:0.9
}
.checkout_right{
    flex:0.3;
    margin-left: 20px;
}
.checkout_left h2{
    font-size:xx-large;
    padding: 20px 15px;
    justify-content: center;
    

}
.checkout_product{
    padding-left:20px;
    padding-right:20px;

    
}
.checkout_checkbox{
    display: flex;
    align-items: center;
    height:30px;
    margin-left:20px;
    margin-right:20px;
    border-bottom: 1px solid silver

}
.checkout_checkbox >label>input[type="checkbox"] {
    margin:10px;
    appearance:none;
    cursor:pointer;
    width:1rem;
    height:1rem;
    background:white;
    border:1px solid rgba(0, 139, 139, 0.63);
    text-align:center;
}
.checkout_checkbox >label>input[type="checkbox"]:before{
    content:"\2714";
    overflow:hidden;
    color:white;
    
    transform: scale(0) translate(-50%,-50%);
    line-height: 1;
}
.checkout_checkbox >label>input[type="checkbox"]:checked{
    background:cadetblue;

}